import BaseLoading from "./UI/BaseLoading.vue";

export default {
    components: { BaseLoading },

    data() {
        return {
            brand: [],
            slickOptions: {
                arows: false,
                slidesToShow: 6,
                autoplay: true,
                autoplaySpeed: 500,

                responsive: [
                    {
                        breakpoint: 1000,
                        settings: {
                            slidesToShow: 5
                        }
                    }
                ]
            },
            isGet: false
        };
    },

    name: "MyComponent",
    // components: { VueSlickCarousel },

    async mounted() {
        let b = await this.$axios.get("/brand/all");

        this.brand = b.data.data;
        this.isGet = true;
    }
};
