export default {
    data() {
        return {
            popular: null,
            page: 1,
            limit: 10
        };
    },
    async mounted() {
        this.getData();
    },
    methods: {
        async getData() {
            this.limit = await this.$store.state.all.limit3;

            this.$axios
                .$post(`product/filter?page=${this.page}&limit=${this.limit}`, {
                    sort: "popular"
                })
                .then(res => {
                    this.popular = res.data;
                });
        },
        updatePopular() {
            this.limit = this.limit + 10;
            this.$store.commit("LIMIT_3", this.limit);

            this.getData();
        }
    }
};
