export default {
    layout: "main__page__layout",
    data() {
        return {
            seach: null
        };
    },
    mounted() {},

    created() {
        window.addEventListener("scroll", this.scrollTop);
        // window.scrollTo({
        //     top: this.$store.state.all.scrollTop
        // });
    },
    beforeDestroy() {
        window.removeEventListener("scroll", this.scrollTop);
    },
    methods: {
        scrollTop() {
            this.$store.commit("SCROLL_TOP", window.scrollY);
        }
    }
};
